        <p>Image Sprtite 기법은 웹에서 자주쓰이는 이미지들을 하나의 큰 이미지(배경이 투명한 이미지)에 필요한 이미지들을 모아 딱 한 번만 이미지를 불러온 후 background-image, background-position 속성을 이용해서 필요한 곳에 필요한 이미지의 부분만 보여주는 방식을 말 합니다.</p>
        
